/*=============================
	1. Google Fonts
===============================*/
@import url("https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:ital,wght@0,200;0,300;0,400;0,500;0,600;1,200;1,300;1,400;1,500;1,600&family=Urbanist:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&display=swap");

/*=============================
	01. Variable CSS
===============================*/
:root {
  --tg-body-font-family: "Plus Jakarta Sans", sans-serif;
  --tg-heading-font-family: "Urbanist", sans-serif;
  --tg-primary-color: #0055ff;
  --tg-secondary-color: #00194c;
  --tg-body-font-color: #334770;
  --tg-heading-font-color: #00194c;
  --tg-paragraph-color: #334770;
  --tg-section-background: #f8faff;
  --tg-body-font-size: 16px;
  --tg-body-font-weight: 500;
  --tg-heading-font-weight: 700;
  --tg-body-line-height: 1.62;
  --tg-heading-line-height: 1.2;
  --tg-text-transform: capitalize;
  --tg-blue: #0049dc;
  --tg-metallic-blue: #324b7d;
  --tg-cornflower-blue: #b3ccff;
  --tg-steel-blue: #b6c1db;
  --tg-lavender: #e6eeff;
  --tg-indigo: #6610f2;
  --tg-purple: #6f42c1;
  --tg-pink: #ff005c;
  --tg-red: #ff3c3c;
  --tg-orange: #fd7e14;
  --tg-yellow: #ffb930;
  --tg-green: #219653;
  --tg-teal: #20c997;
  --tg-cyan: #0dcaf0;
  --tg-white: #ffffff;
  --tg-black: #000;
  --tg-gray: #f1f4fc;
  --tg-gray-two: #f1f5ff;
  --tg-gray-three: #96a1b8;
  --tg-gray-four: #667594;
  --tg-border-color: #e1e5ee;
  --facebook: #3b5998;
  --twitter: #00acee;
  --linkedin: #1976d2;
  --pinterest: #3b5998;
  --youtube: #c4302b;
  --skype: #00aff0;
}

body {
  font-family: var(--tg-body-font-family);
  font-weight: var(--tg-body-font-weight);
  font-size: var(--tg-body-font-size);
  line-height: var(--tg-body-line-height);
  color: var(--tg-body-font-color);
  font-style: normal;
}

img,
.img {
  max-width: 100%;
  transition: all 0.3s ease-out 0s;
}

.f-left {
  float: left;
}

.f-right {
  float: right;
}

.fix {
  overflow: hidden;
}

a,
button {
  -webkit-transition: all 0.3s ease-out 0s;
  -moz-transition: all 0.3s ease-out 0s;
  -ms-transition: all 0.3s ease-out 0s;
  -o-transition: all 0.3s ease-out 0s;
  transition: all 0.3s ease-out 0s;
  text-decoration: none;
}

a:focus,
.btn:focus,
button:focus {
  text-decoration: none;
  outline: none;
  box-shadow: none;
}

a:hover,
.portfolio-cat a:hover,
.footer -menu li a:hover {
  color: var(--tg-primary-color);
  text-decoration: none;
}

a,
button {
  color: var(--tg-primary-color);
  outline: medium none;
  text-decoration: none;
}

.btn:focus,
button:focus,
input:focus,
input:focus,
textarea,
textarea:focus {
  outline: 0;
}

.uppercase {
  text-transform: uppercase;
}

.capitalize {
  text-transform: capitalize;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--tg-heading-font-family);
  color: var(--tg-heading-font-color);
  margin-top: 0px;
  margin-bottom: 0.7rem;
  font-style: normal;
  line-height: var(--tg-heading-line-height);
  font-weight: var(--tg-heading-font-weight);
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: inherit;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

.list-wrap {
  margin: 0px;
  padding: 0px;
}

.list-wrap li {
  list-style: none;
}

p {
  font-size: var(--tg-body-font-size);
  font-weight: var(--tg-body-font-weight);
  line-height: var(--tg-body-line-height);
  color: var(--tg-paragraph-color);
  text-transform: var(--tg-text-transform);
  margin-bottom: 15px;
}

hr {
  border-bottom: 1px solid var(--tg-primary-color);
  border-top: 0 none;
  margin: 30px 0;
  padding: 0;
}

label {
  color: var(--tg-body-font-color);
  cursor: pointer;
  font-size: 16px;
  font-weight: 400;
}

*::-moz-selection {
  background: var(--tg-primary-color);
  color: var(--tg-white);
  text-shadow: none;
}

::-moz-selection {
  background: var(--tg-primary-color);
  color: var(--tg-white);
  text-shadow: none;
}

::selection {
  background: var(--tg-primary-color);
  color: var(--tg-white);
  text-shadow: none;
}

*::-moz-placeholder {
  color: var(--tg-body-font-color);
  font-size: var(--tg-body-font-size);
  opacity: 1;
}

*::placeholder {
  color: var(--tg-body-font-color);
  font-size: var(--tg-body-font-size);
  opacity: 1;
}

.theme-overlay {
  position: relative;
}

.theme-overlay::before {
  background: var(--tg-primary-color) none repeat scroll 0 0;
  content: "";
  height: 100%;
  left: 0;
  opacity: 0.6;
  position: absolute;
  top: 0;
  width: 100%;
}

.separator {
  border-top: 1px solid var(--tg-primary-color);
}

/* Bootstrap 5 */
.container {
  padding-left: 15px;
  padding-right: 15px;
}

.row {
  --bs-gutter-x: 30px;
}

.row.g-0 {
  --bs-gutter-x: 0;
}

.gutter-y-30 {
  --bs-gutter-y: 30px;
}

/*=============================
	1. Button style
===============================*/
.btn {
  user-select: none;
  -moz-user-select: none;
  background: var(--tg-primary-color) none repeat scroll 0 0;
  border: medium none;
  border-radius: 30px;
  color: var(--tg-white);
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.02em;
  line-height: 1;
  margin-bottom: 0;
  padding: 18px 18px 18px 22px;
  text-align: center;
  text-transform: uppercase;
  touch-action: manipulation;
  transition: all 0.3s ease 0s;
  vertical-align: middle;
  white-space: nowrap;
  border: 0;
  position: relative;
  z-index: 1;
}

.btn::after {
  content: "\f054";
  font-family: "Font Awesome 5 Free";
  font-weight: 700;
  margin-left: 45px;
}

.btn::before {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 50px;
  height: 50px;
  background: var(--tg-blue);
  border-radius: 30px;
  z-index: -1;
  transition: all 0.3s ease 0s;
}

.btn:hover::before {
  width: 100%;
}

.btn:hover {
  color: var(--tg-white);
}

.btn.btn-two::after {
  display: none;
}

.transparent-btn {
  border: 2px solid var(--tg-primary-color);
  background: transparent;
  color: var(--tg-primary-color);
}

.btn-three {
  padding: 19px 30px;
}

.transparent-btn-two::before,
.btn-three::before {
  display: none;
}

.transparent-btn-two::after,
.btn-three::after {
  content: "";
  background-image: url(../img/icons/right-arrow.svg);
  filter: brightness(0) invert(1);
  width: 18px;
  height: 12px;
  margin-left: 12px;
}

.btn-three:hover {
  background: var(--tg-secondary-color);
}

.transparent-btn-two {
  border: 2px solid var(--tg-primary-color);
  color: var(--tg-primary-color);
  background: transparent;
  padding: 17px 30px;
}

.transparent-btn-two::after {
  filter: none;
  transition: all 0.3s ease 0s;
}

.transparent-btn-two:hover {
  background: var(--tg-primary-color);
}

.transparent-btn-two:hover::after {
  filter: brightness(0) invert(1);
}

.breadcrumb>.active {
  color: var(--tg-primary-color);
}

/* scrollUp */
.scroll-top {
  width: 35px;
  height: 35px;
  line-height: 35px;
  position: fixed;
  bottom: -10%;
  right: 30px;
  font-size: var(--tg-body-font-size);
  border-radius: 6px;
  z-index: 99;
  color: var(--tg-white);
  text-align: center;
  cursor: pointer;
  background: var(--tg-primary-color);
  transition: 1s ease;
  border: none;
}

.scroll-top.open {
  bottom: 30px;
}

.scroll-top::after {
  position: absolute;
  z-index: -1;
  content: "";
  top: 100%;
  left: 5%;
  height: 10px;
  width: 90%;
  opacity: 1;
  background: radial-gradient(ellipse at center,
      rgba(0, 0, 0, 0.25) 0%,
      rgba(0, 0, 0, 0) 80%);
}

.scroll-top:hover {
  background: var(--tg-secondary-color);
}

/*=============================
	02. Header
===============================*/
.custom-container {
  max-width: 1310px;
}

.custom-container-two {
  max-width: 1360px;
}

.custom-container-three {
  max-width: 1800px;
}

.transparent-header {
  position: absolute;
  left: 0;
  top: 0px;
  width: 100%;
  z-index: 9;
  height: auto;
}

#header-fixed-height.active-height {
  display: block;
  height: 96px;
}

.heder-top-wrap {
  background: var(--tg-secondary-color);
  overflow: hidden;
  padding: 9px 0;
}

.header-top-left .list-wrap {
  display: flex;
  align-items: center;
  gap: 10px 35px;
}

.header-top-left .list-wrap li {
  font-size: 14px;
  color: var(--tg-lavender);
  display: flex;
  align-items: center;
  line-height: 1.28;
  position: relative;
}

.header-top-left .list-wrap li::before {
  content: "";
  position: absolute;
  right: -16px;
  top: 50%;
  transform: translateY(-50%);
  width: 1px;
  height: 500%;
  background: var(--tg-metallic-blue);
}

.header-top-left .list-wrap li:last-child::before {
  display: none;
}

.header-top-left .list-wrap li i {
  font-size: 20px;
  margin-right: 8px;
  line-height: 0;
  color: var(--tg-cornflower-blue);
}

.header-top-left .list-wrap li a {
  color: var(--tg-lavender);
}

.header-top-left .list-wrap li a:hover {
  color: var(--tg-primary-color);
}

.header-top-right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.header-contact {
  position: relative;
  margin-right: 35px;
}

.header-contact::before {
  content: "";
  position: absolute;
  right: -16px;
  top: 50%;
  transform: translateY(-50%);
  width: 1px;
  height: 500%;
  background: var(--tg-metallic-blue);
}

.header-contact a {
  font-size: 14px;
  line-height: 1.28;
  color: var(--tg-lavender);
  display: flex;
  align-items: center;
}

.header-contact a i {
  font-size: 20px;
  margin-right: 8px;
}

.header-contact a:hover {
  color: var(--tg-primary-color);
}

.header-social .list-wrap {
  display: flex;
  align-items: center;
  gap: 12px;
}

.header-social .list-wrap li a {
  font-size: 15px;
  color: var(--tg-lavender);
}

.header-social .list-wrap li a:hover {
  color: var(--tg-primary-color);
}

.menu-nav {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.logo img,
.f-logo img,
.fw-logo img {
  max-height: 29px;
}

.navbar-wrap {
  display: flex;
  flex-grow: 1;
}

.navbar-wrap ul {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0 0;
  margin: 0 0 0 auto;
}

.navbar-wrap ul li {
  list-style: none;
  display: block;
  position: relative;
}

.navbar-wrap ul li a {
  font-size: 16px;
  font-weight: 500;
  text-transform: capitalize;
  color: var(--tg-secondary-color);
  padding: 40px 15px;
  display: block;
  line-height: 1;
  position: relative;
  z-index: 1;
}

.navbar-wrap>ul>li.active>a,
.navbar-wrap>ul>li:hover>a {
  color: var(--tg-primary-color);
}

.navbar-wrap>ul>li.menu-item-has-children>a::after {
  content: "\f107";
  font-weight: 700;
  font-family: "Font Awesome 5 Free";
  color: var(--tg-paragraph-color);
  margin-left: 8px;
  transition: all 0.3s ease-out 0s;
}

.navbar-wrap>ul>li.menu-item-has-children:hover>a::after,
.navbar-wrap>ul>li.menu-item-has-children.active>a::after {
  color: var(--tg-primary-color);
}

.main-menu .navigation li.menu-item-has-children .dropdown-btn {
  display: none;
}

.header-action>ul {
  display: flex;
  align-items: center;
  margin-left: 25px;
}

.header-action>ul li {
  position: relative;
  margin-left: 25px;
}

.header-action ul li:first-child {
  margin-left: 0;
}

.header-action ul li a {
  color: var(--tg-heading-font-color);
  font-size: 14px;
}

.header-action ul li.header-search {
  line-height: 0;
}

.header-action ul li.header-search a {
  color: #334770;
  font-size: 22px;
  line-height: 0;
  transition: all 0.3s ease-out 0s;
}

.header-action ul li.header-search a:hover {
  color: var(--tg-primary-color);
}

.header-action .header-btn .btn {
  color: var(--tg-white);
  font-size: 15px;
  padding: 16px 33px;
  border: 2px solid transparent;
}

.header-action .header-btn .btn::before {
  display: none;
}

.header-action .header-btn .btn:hover {
  background: transparent;
  border-color: var(--tg-primary-color);
  color: var(--tg-primary-color);
}

.navbar-wrap ul li .sub-menu {
  position: absolute;
  left: 0;
  right: 0;
  top: 100%;
  min-width: 230px;
  border: 1px solid #f5f5f5;
  background: var(--tg-white);
  margin: 0 0;
  transform: scale(1, 0);
  transform-origin: 0 0;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  -webkit-box-shadow: 0px 30px 70px 0px rgba(137, 139, 142, 0.15);
  -moz-box-shadow: 0px 30px 70px 0px rgba(137, 139, 142, 0.15);
  box-shadow: 0px 30px 70px 0px rgba(137, 139, 142, 0.15);
  border-radius: 5px;
  padding: 18px 0;
  display: block;
  visibility: hidden;
  opacity: 0;
  z-index: 9;
}

.navbar-wrap ul li .sub-menu .sub-menu {
  right: auto;
  left: 100%;
  top: 0;
}

.navbar-wrap ul li .sub-menu li {
  margin-left: 0;
  text-align: left;
  display: block;
}

.navbar-wrap ul li .sub-menu li a {
  padding: 9px 15px 9px 25px;
  line-height: 1.4;
  font-size: 16px;
  font-weight: 500;
  color: var(--tg-secondary-color);
  text-transform: capitalize;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  display: flex;
  align-items: center;
  gap: 5px;
}

.navbar-wrap ul li .sub-menu li a::before {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  border-radius: 2px;
  background: var(--tg-primary-color);
  transition: 0.3s ease-in-out;
}

.navbar-wrap ul li .sub-menu>li.active>a,
.navbar-wrap ul li .sub-menu li a:hover {
  color: var(--tg-primary-color);
}

.navbar-wrap ul li .sub-menu>li.active>a::before,
.navbar-wrap ul li .sub-menu li a:hover::before {
  width: 10px;
}

.navbar-wrap ul li:hover>.sub-menu {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
}

.sticky-menu {
  position: fixed;
  left: 0;
  margin: auto;
  top: 0;
  width: 100%;
  z-index: 99;
  background: var(--tg-white);
  -webkit-animation: 1000ms ease-in-out 0s normal none 1 running fadeInDown;
  animation: 1000ms ease-in-out 0s normal none 1 running fadeInDown;
  -webkit-box-shadow: 0 10px 15px rgba(25, 25, 25, 0.1);
  box-shadow: 0 10px 15px rgba(25, 25, 25, 0.1);
  border-radius: 0;
}

